*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  #root{
    min-width: 250px;
  }
  
  html{
    font-size: 62.5%;
  }
  
  body{
    font-size: 1.8rem;
  }
  .App {
    text-align: center;
  }
  
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  
  .loader{
    display: flex;
    height: 75px;
    width: 120px;
    flex-wrap: wrap;
    align-items:flex-end;
    justify-content: space-between;
    margin: 20px auto;
    color:#fff;
  }
  
  .loader span{
    font-size: 2.2rem;
    text-transform: uppercase;
    margin: auto;
  }
  
  .ball{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #fff;
    animation: bounce .5s alternate infinite;
  }
  
  .ball:nth-child(2){
    animation-delay: .16s;
  }
  
  .ball:nth-child(3){
    animation-delay: .32s;
  }
  
  
  .notodos,
  .noresult{
    display: flex;
    flex-direction: column;
    width: 50%;
    height: auto;
    margin: 0 auto;
    gap: 30px;
  }
  
  .notodos h2,
  .noresult h2{
    text-align: center;
  }
  
  
  .img-wrapper{
    display: flex;
    max-width: 250px;
    position: relative;
    margin: 0 auto;
  }
  
  .img-wrapper img{
    width: 100%;
    display: block;
    border-radius:50%;
  }
  
  .img-wrapper::before{
    content: "";
    position: absolute;
    inset: -15px;
    border: 6px solid transparent;
    border-top-color: #fff;
    border-bottom-color: #ababab;
    border-radius:50%;
    animation: spin 3s linear infinite;
  }
  
  .error{
    display: flex;
    flex-direction: column;
    width: 20%;
    margin: 0 auto;
  }
  
  .error img{
    width: 100%;
  }
  
  .error h2{
    text-align: center;
    color:white;
  }
  