.NewTask{
    font-weight: bold;
    letter-spacing: 1px;
    background-color: #51d1ee;
    box-shadow: #094c66 4px 4px 0;
    color: rgb(22, 22, 22);
    border-radius: 8px;
    transition: transform 200ms,box-shadow 200ms;
    cursor: pointer;
    padding: 10px;
    width: 100px;
    margin: 10px calc(50% - 50px);
    position: absolute;
    
}

button:active {
    transform: translateY(4px) translateX(4px);
    box-shadow: #094c66 0px 0px 0px;
}
